<template>
	<div id="app">
		<!--<transition name="bounce">-->
		<router-view></router-view>
		<!--</transition>-->
	</div>
</template>

<script>
	import ApiService from 'SERVICES/api.service';
	export default {
		name: 'app',
		components: {
		},
		mounted: function () {

			setInterval(() => {
				if (sessionStorage.getItem('ssId')) {
					this.refreshToken();
				} 
			}, 1000*60*25);


		},
		methods: {
			refreshToken() {
				ApiService.get({
					'ext': 'system/session/info',
					'ss_id': sessionStorage.getItem('ssId')
				}).then(res => {
					console.log(res.data);
				})
			},
		}

	}

</script>

<style>
	@import "../node_modules/font-awesome/css/font-awesome.min.css";
	@import "../node_modules/flex.css/dist/flex.css";
	.bounce-enter-active {
		animation: bounce-in .5s;
	}
	
	.bounce-leave-active {
		animation: bounce-out .2s;
	}
	
	@keyframes bounce-in {
		0% {
			transform: scale(0);
		}
		50% {
			transform: scale(1.05);
		}
		100% {
			transform: scale(1);
		}
	}
	
	@keyframes bounce-out {
		0% {
			transform: scale(1);
		}
		50% {
			transform: scale(0.95);
		}
		100% {
			transform: scale(0);
		}
	}
	
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity .5s
	}
	
	.fade-enter,
	.fade-leave-active {
		opacity: 0
	}
	
	body {
		/*background-color: #324057;*/
		margin: 0px;
		padding: 0px;
		/*background: url(assets/bg1.jpg) center !important;
		background-size: cover;*/
		background: #1F2D3D;
		font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
		/*font-weight: 400;*/
		font-size: 14px;
		-webkit-font-smoothing: antialiased;
	}
	
	#app {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
	}
	
	.el-submenu [class^=fa] {
		vertical-align: baseline;
		margin-right: 10px;
	}
	
	.el-menu-item [class^=fa] {
		vertical-align: baseline;
		margin-right: 10px;
	}
	
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	
	.el-pagination {
		float: right;
	}
	
	.toolbar {
		background: #fff;
		padding: 10px 10px 10px 10px;
	}
</style>


